.feedback .container{ 
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    position: relative;
    top: 25%;
    left: 10%;
    margin: -19px;
    box-shadow: -5px 6px 22px 3px rgb(123 123 123 / 36%);
    height: 65%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-radius: 14px;
    height: 68vh;
    width: 67vw;
}
.box{
    width: 100%; 
    margin: 22px 0px;
    border-bottom: 2px solid purple;
}


.box input{  
    background: none;
    width: 75%;
    padding: 5px 10px;
    font-size: 20px;
    border: none;
    outline:none;
    color: black;
}


.feedback .submitbtn{
    transition: all 1s;
    color: white;
    width: 8rem;
    background-color: #d13d4b;
    /* margin-left: 50px; */
}
.feedback .submitbtn:hover{
    box-shadow: -5px 6px 22px 3px rgb(123 123 123 / 36%);
    color: white;
}

