.main {
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 83vh;
}

.main-div-temp {
  box-shadow: -5px 6px 19px 0px rgb(0 0 0 / 21%);
  background-color: white;
  height: 72vh;
  width: 32vw;
  border-radius: 21px;
  position: absolute;
  top: 118px;
}

.search {
  position: relative;
  top: -3rem;
  left: 3.5rem;
}
.cityNameinp {
  font-family: "Source Sans Pro", sans-serif;
  font-size: 1.5rem;
  border: 2px solid #0048ff;
  outline: none;
  border-radius: 7px;
  background: whitesmoke;
  height: 2rem;
  width: 15rem;
}

.btnSearch {
  background: rgb(26, 26, 241);
  color: white;
  outline: none;
  border: none;
  margin-left: 5px;
  height: 2rem;
  width: 4rem;
  border-radius: 7px;
}

.spinnerW {
  display: inline-block;
  position: absolute;
  top: 12rem;
  left: 40rem;
}

/* .more-temp{
