*/
.humidityimage {
  height: 10vh;
}

.widget .extra-temp {
  flex: 1 1 100%;
  padding: 5% 0;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  background: #fafafa;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  font-size: 100px;
}

.widget .extra-temp p {
  font-size: 25px;
  font-weight: 600;
}

.temp-info-minmax,
.weather-extra-info,
.two-sided-section {
  display: grid;
  margin-top: 12px;
  margin-bottom: -5px;
  grid-template-columns: repeat(2, 1fr);
  text-align: center;
  align-items: center;
  /* background-color: blanchedalmond; */
  /* margin: 14px; */
  /* background-color: blanchedalmond; */
}

.widget .extra-temp .extra-info-leftside {
  text-align: left;
  font-weight: 600;
  text-transform: capitalize;
  color: #080705;
  font-size: 15px;
}

.extra-info-leftside {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  margin-top: 0;
  margin-bottom: 0.5rem;
  font-weight: 500;
  font-size: 26px;
  line-height: 1.2;
}

@media screen and (max-height: 600px) {
  .main-div-temp {
    position: relative;
  }
  .search{
      width: 20rem;
      display: flex;
  }
}

